// Content

body {
    font-family: sans-serif;
    color: #444444;
    text-align: center;
    padding: 20px;
}

h1 {
    font-size: 2rem;
    font-weight: 700;
    line-height: 3rem;
}

h2 {
    font-size: 1.25rem;
}

p {
    border-top: 1px solid #444444;
    border-bottom: 1px solid #444444;
    margin-top: 5px;
    display: inline-block;
    padding: 5px 0;
}